      //NickPattern 16
      const NickPattern = "^[\u4e00-\u9fa5_a-zA-Z0-9-]{1,16}$";

      //UserPattern 416
      const UserPattern = "^[a-zA-Z0-9_]{4,16}$";

      //PassPattern 6~18
      const PassPattern ="^[a-zA-Z0-9_]{5,17}$";


      export default {
        nickname:NickPattern,username:UserPattern,password:PassPattern
      }